body {
  overflow-x: hidden;
}
body header.top-navigation .dropdown-menu {
  margin-top: 1rem;
}
body header.top-navigation .search-navbar .select2-selection .select2-selection--single {
  border-radius: 16px;
}
body header.top-navigation .search-navbar .select2-container--krajee-bs5 .select2-selection {
  background-color: transparent;
  border-radius: 16px;
}
body header.top-navigation .search-navbar .select2-container--krajee-bs5 .select2-selection--single .select2-selection__rendered {
  color: #212529;
  padding-left: 8px;
  padding-right: 8px;
}
body header.top-navigation .navbar-toggler {
  border: none;
}
body main {
  padding-top: 56px;
}
body main aside.left-navigation {
  background-color: #fff;
  z-index: 4;
  height: 100%;
  position: fixed;
  top: 0;
  bottom: 0;
  padding: 56px 0 0;
  transition: all 0.25s;
  overflow-y: auto;
}
body main aside.left-navigation ul {
  padding: 0.5em 0.5em 0;
}
body main aside.left-navigation ul li {
  list-style: none;
  line-height: 2.5rem;
  margin-bottom: 0.25rem;
}
body main aside.left-navigation ul li:hover {
  color: #E8AA42;
  border-radius: 8px;
}
body main aside.left-navigation ul li a {
  display: block;
  text-decoration: none;
  color: #000;
}
body main aside.left-navigation ul li.active a.active {
  border-radius: 8px;
  background-color: #231955;
  color: #fff;
}
body main section.content {
  padding: 1rem;
  margin-left: 250px;
  transition: all 0.1s;
}
body footer {
  padding: 1rem;
  margin-left: 250px;
}

/* all screen include ipad mini */
@media only screen and (min-width: 768px) {
  body main aside.left-navigation {
    width: 250px;
    min-height: 100vh;
    z-index: 1;
    border-right: 1px solid #e9ecef;
  }
}
/* Mainstream tablet */
@media (max-width: 767px) {
  body main aside.left-navigation {
    width: 0;
    min-height: 100vh;
    z-index: 1;
    border-right: none;
  }
  body main section.content {
    margin-left: 0;
  }
  body footer {
    margin-left: 0;
  }
}
body.dark {
  background-color: #343a40 !important;
  color: #fff !important;
}

body.dark input.form-control,
body.dark select.form-control,
body.dark textarea,
body.dark table,
body.dark table th,
body.dark table td,
body.dark div.card,
body.dark div.card .card-header,
body.dark div.card .card-footer,
body.dark aside,
body.dark nav.card,
body.dark ul.pagination li.page-item a.page-link,
body.dark footer {
  background-color: #343a40 !important;
  color: #fff !important;
}

body.dark .dropdown-menu {
  border: 1px solid #fff !important;
  background-color: #343a40 !important;
  color: #fff !important;
}

body.dark a, body.dark i.bi {
  color: #fff !important;
}

body.dark .dropdown-item:hover {
  background-color: #231955 !important;
}

body.dark ul.pagination li.page-item.active a.page-link {
  color: #212529 !important;
  background-color: #fff !important;
  border-color: transparent;
}

.user-photo-32 {
  border-radius: 50%;
  object-fit: cover;
}